<template>
	<div>
		<el-dialog v-model="formVisible" :title="formTitle" width="80%" destroy-on-close :fullscreen='false'>
			<el-form class="formModel_form" ref="formRef" :model="form" label-width="$template2.back.add.form.base.labelWidth" :rules="rules">
				<el-row>
					<el-col :span="12">
						<el-form-item label="手机" prop="shouji">
							<el-input class="list_inp" v-model="form.shouji" placeholder="手机"
								 type="text" 								:readonly="!isAdd||disabledForm.shouji?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="姓名" prop="xingming">
							<el-input class="list_inp" v-model="form.xingming" placeholder="姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.xingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="医生手机" prop="yishengshouji">
							<el-input class="list_inp" v-model="form.yishengshouji" placeholder="医生手机"
								 type="text" 								:readonly="!isAdd||disabledForm.yishengshouji?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="医生姓名" prop="yishengxingming">
							<el-input class="list_inp" v-model="form.yishengxingming" placeholder="医生姓名"
								 type="text" 								:readonly="!isAdd||disabledForm.yishengxingming?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="病史记录" prop="bingshijilu">
							<el-input class="list_inp" v-model="form.bingshijilu" placeholder="病史记录"
								 type="text" 								:readonly="!isAdd||disabledForm.bingshijilu?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="诊断结果" prop="zhenduanjieguo">
							<el-input class="list_inp" v-model="form.zhenduanjieguo" placeholder="诊断结果"
								 type="text" 								:readonly="!isAdd||disabledForm.zhenduanjieguo?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="12">
						<el-form-item label="需要注意" prop="xuyaozhuyi">
							<el-input class="list_inp" v-model="form.xuyaozhuyi" placeholder="需要注意"
								 type="text" 								:readonly="!isAdd||disabledForm.xuyaozhuyi?true:false" />
						</el-form-item>
					</el-col>

					<el-col :span="24">
						<el-form-item prop="dangantupian"
									  label="档案图片"
						>
							<uploads
								:disabled="!isAdd||disabledForm.dangantupian?true:false"
								action="file/upload"

								tip="请上传档案图片"
								:limit="3"
								style="width: 100%;text-align: left;"
								:fileUrls="form.dangantupian?form.dangantupian:''" 
								@change="dangantupianUploadSuccess">
							</uploads>
						</el-form-item>
					</el-col>
					<el-col :span="24">
						<el-form-item label="诊断详情" prop="zhenduanxiangqing">
							<editor :value="form.zhenduanxiangqing" placeholder="请输入诊断详情" :readonly="!isAdd||disabledForm.zhenduanxiangqing?true:false"
								class="list_editor" @change="(e)=>editorChange(e,'zhenduanxiangqing')"></editor>
						</el-form-item>
					</el-col>
				</el-row>
			</el-form>
			<template #footer v-if="isAdd||type=='logistics'||type=='reply'">
				<span class="formModel_btn_box">
					<el-button class="formModel_cancel" @click="closeClick">取消</el-button>
					<el-button class="formModel_confirm" type="primary" @click="save"
						>
						提交
					</el-button>
				</span>
			</template>
		</el-dialog>
	</div>
</template>
<script setup>
	import {
		reactive,
		ref,
		getCurrentInstance,
		nextTick,
		computed,
		defineEmits
	} from 'vue'
	const context = getCurrentInstance()?.appContext.config.globalProperties;	
	const emit = defineEmits(['formModelChange'])
	//基础信息
	const tableName = 'jiankangdangan'
	const formName = '健康档案'
	//基础信息
	//form表单
	const form = ref({})
	const disabledForm = ref({
		shouji : false,
		xingming : false,
		yishengshouji : false,
		yishengxingming : false,
		bingshijilu : false,
		zhenduanjieguo : false,
		zhenduanxiangqing : false,
		xuyaozhuyi : false,
		dangantupian : false,
		crossuserid : false,
		crossrefid : false,
	})
	const formVisible = ref(false)
	const isAdd = ref(false)
	const formTitle = ref('')
	//表单验证
	//匹配整数
	const validateIntNumber = (rule, value, callback) => {
		if (!value) {
			callback();
		} else if (!context?.$toolUtil.isIntNumer(value)) {
			callback(new Error("请输入整数"));
		} else {
			callback();
		}
	}
	//匹配数字
	const validateNumber = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isNumber(value)) {
			callback(new Error("请输入数字"));
		} else {
			callback();
		}
	}
	//匹配手机号码
	const validateMobile = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isMobile(value)) {
			callback(new Error("请输入正确的手机号码"));
		} else {
			callback();
		}
	}
	//匹配电话号码
	const validatePhone = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isPhone(value)) {
			callback(new Error("请输入正确的电话号码"));
		} else {
			callback();
		}
	}
	//匹配邮箱
	const validateEmail = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isEmail(value)) {
			callback(new Error("请输入正确的邮箱地址"));
		} else {
			callback();
		}
	}
	//匹配身份证
	const validateIdCard = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.checkIdCard(value)) {
			callback(new Error("请输入正确的身份证号码"));
		} else {
			callback();
		}
	}
	//匹配网站地址
	const validateUrl = (rule, value, callback) => {
		if(!value){
			callback();
		} else if (!context?.$toolUtil.isURL(value)) {
			callback(new Error("请输入正确的URL地址"));
		} else {
			callback();
		}
	}
	const rules = ref({
		shouji: [
		],
		xingming: [
		],
		yishengshouji: [
		],
		yishengxingming: [
		],
		bingshijilu: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		zhenduanjieguo: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		zhenduanxiangqing: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		xuyaozhuyi: [
			{required: true,message: '请输入',trigger: 'blur'}, 
		],
		dangantupian: [
		],
		crossuserid: [
		],
		crossrefid: [
		],
	})
	//表单验证
	
	const formRef = ref(null)
	const id = ref(0)
	const type = ref('')
	//档案图片上传回调
	const dangantupianUploadSuccess=(e)=>{
		form.value.dangantupian = e
	}
	//methods

	//获取唯一标识
	const getUUID =()=> {
      return new Date().getTime();
    }
	//重置
	const resetForm = () => {
		form.value = {
			shouji: '',
			xingming: '',
			yishengshouji: '',
			yishengxingming: '',
			bingshijilu: '',
			zhenduanjieguo: '',
			zhenduanxiangqing: '',
			xuyaozhuyi: '',
			dangantupian: '',
			crossuserid: '',
			crossrefid: '',
		}
	}
	//获取info
	const getInfo = ()=>{
		context?.$http({
			url: `${tableName}/info/${id.value}`,
			method: 'get'
		}).then(res => {
			let reg=new RegExp('../../../file','g')
			res.data.data.zhenduanxiangqing = res.data.data.zhenduanxiangqing?(res.data.data.zhenduanxiangqing.replace(reg,'../../../cl85104220/file')):'';
			form.value = res.data.data
			formVisible.value = true
		})
	}
	const crossRow = ref('')
	const crossTable = ref('')
	const crossTips = ref('')
	const crossColumnName = ref('')
	const crossColumnValue = ref('')
	//初始化
	const init=(formId=null,formType='add',formNames='',row=null,table=null,statusColumnName=null,tips=null,statusColumnValue=null)=>{
		resetForm()
		if(formId){
			id.value = formId
			type.value = formType
		}
		if(formType == 'add'){
			isAdd.value = true
			formTitle.value = '新增' + formName
			formVisible.value = true
		}else if(formType == 'info'){
			isAdd.value = false
			formTitle.value = '查看' + formName
			getInfo()
		}else if(formType == 'edit'){
			isAdd.value = true
			formTitle.value = '修改' + formName
			getInfo()
		}
		else if(formType == 'cross'){
			isAdd.value = true
			formTitle.value = formNames
			// getInfo()
			for(let x in row){
				if(x=='shouji'){
					form.value.shouji = row[x];
					disabledForm.value.shouji = true;
					continue;
				}
				if(x=='xingming'){
					form.value.xingming = row[x];
					disabledForm.value.xingming = true;
					continue;
				}
				if(x=='yishengshouji'){
					form.value.yishengshouji = row[x];
					disabledForm.value.yishengshouji = true;
					continue;
				}
				if(x=='yishengxingming'){
					form.value.yishengxingming = row[x];
					disabledForm.value.yishengxingming = true;
					continue;
				}
				if(x=='bingshijilu'){
					form.value.bingshijilu = row[x];
					disabledForm.value.bingshijilu = true;
					continue;
				}
				if(x=='zhenduanjieguo'){
					form.value.zhenduanjieguo = row[x];
					disabledForm.value.zhenduanjieguo = true;
					continue;
				}
				if(x=='zhenduanxiangqing'){
					form.value.zhenduanxiangqing = row[x];
					disabledForm.value.zhenduanxiangqing = true;
					continue;
				}
				if(x=='xuyaozhuyi'){
					form.value.xuyaozhuyi = row[x];
					disabledForm.value.xuyaozhuyi = true;
					continue;
				}
				if(x=='dangantupian'){
					form.value.dangantupian = row[x];
					disabledForm.value.dangantupian = true;
					continue;
				}
				if(x=='crossuserid'){
					form.value.crossuserid = row[x];
					disabledForm.value.crossuserid = true;
					continue;
				}
				if(x=='crossrefid'){
					form.value.crossrefid = row[x];
					disabledForm.value.crossrefid = true;
					continue;
				}
			}
			if(row){
				crossRow.value = row
			}
			if(table){
				crossTable.value = table
			}
			if(tips){
				crossTips.value = tips
			}
			if(statusColumnName){
				crossColumnName.value = statusColumnName
			}
			if(statusColumnValue){
				crossColumnValue.value = statusColumnValue
			}
			formVisible.value = true
		}

		context?.$http({
			url: `${context?.$toolUtil.storageGet('sessionTable')}/session`,
			method: 'get'
		}).then(res => {
			var json = res.data.data
			if(json.hasOwnProperty('yishengshouji')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.yishengshouji = json.yishengshouji
				disabledForm.value.yishengshouji = true;
			}
			if(json.hasOwnProperty('yishengxingming')&& context?.$toolUtil.storageGet("role")!="管理员"){
				form.value.yishengxingming = json.yishengxingming
				disabledForm.value.yishengxingming = true;
			}
		})
	}
	//初始化
	//声明父级调用
	defineExpose({
		init
	})
	//关闭
	const closeClick = () => {
		formVisible.value = false
	}
	//富文本
	const editorChange = (e,name) =>{
		form.value[name] = e
	}
	//提交
	const save=()=>{
		if(form.value.dangantupian!=null) {
			form.value.dangantupian = form.value.dangantupian.replace(new RegExp(context?.$config.url,"g"),"");
		}
		var table = crossTable.value
		var objcross = JSON.parse(JSON.stringify(crossRow.value))
		let crossUserId = ''
		let crossRefId = ''
		let crossOptNum = ''
		if(type.value == 'cross'){
			if(crossColumnName.value!=''){
				if(!crossColumnName.value.startsWith('[')){
					for(let o in objcross){
						if(o == crossColumnName.value){
							objcross[o] = crossColumnValue.value
						}
					}
					//修改跨表数据
					changeCrossData(objcross)
				}else{
					crossUserId = context?.$toolUtil.storageGet('userid')
					crossRefId = objcross['id']
					crossOptNum = crossColumnName.value.replace(/\[/,"").replace(/\]/,"")
				}
			}
		}
		formRef.value.validate((valid)=>{
			if(valid){
				if(crossUserId&&crossRefId){
					form.value.crossuserid = crossUserId
					form.value.crossrefid = crossRefId
					let params = {
						page: 1,
						limit: 1000, 
						crossuserid:form.value.crossuserid,
						crossrefid:form.value.crossrefid,
					}
					context?.$http({
						url: `${tableName}/page`,
						method: 'get', 
						params: params 
					}).then(res=>{
						if(res.data.data.total>=crossOptNum){
							context?.$toolUtil.message(`${crossTips.value}`,'error')
							return false
						}else{
							context?.$http({
								url: `${tableName}/${!form.value.id ? "save" : "update"}`,
								method: 'post', 
								data: form.value 
							}).then(res=>{
								context?.$toolUtil.message(`操作成功`,'success',()=>{
									formVisible.value = false
									emit('formModelChange')
								})
							})
						}
					})
				}else{
					context?.$http({
						url: `${tableName}/${!form.value.id ? "save" : "update"}`,
						method: 'post', 
						data: form.value 
					}).then(res=>{
						context?.$toolUtil.message(`操作成功`,'success',()=>{
							formVisible.value = false
							emit('formModelChange')
						})
					})
				}
			}
		})
	}
	//修改跨表数据
	const changeCrossData=(row)=>{
		context?.$http({
			url: `${crossTable.value}/update`,
			method: 'post',
			data: row
		}).then(res=>{})
	}
</script>
<style lang="scss" scoped>
	// 表单
	.formModel_form{
		border: 0px solid #ddd;
		border-radius: 4px;
		padding: 30px;
		margin: 0;
		background: none;
		// form item
		:deep(.el-form-item) {
			margin: 0 150px 20px 0;
			background: none;
			display: flex;
			//label
			.el-form-item__label {
			 background: none;
			 font-weight: 500;
			 display: block;
			 width: 150px;
			 min-width: 150px;
			 text-align: right;
			}
			// 内容盒子
			.el-form-item__content {
				display: flex;
				width: calc(100% - 120px);
				justify-content: flex-start;
				align-items: center;
				flex-wrap: wrap;
				// 输入框
				.list_inp {
					border: 0px solid #ddd;
					border-radius: 30px;
					padding: 0 10px;
					background: #C61C1420;
					width: auto;
					line-height: 36px;
					box-sizing: border-box;
					min-width: 350px;
					height: 36px;
					//去掉默认样式
					.el-input__wrapper{
						border: none;
						box-shadow: none;
						background: none;
						border-radius: 0;
						height: 100%;
						padding: 0;
					}
					.is-focus {
						box-shadow: none !important;
					}
				}
				// 富文本
				.list_editor {
					background-color: #C61C1420;
					border-radius: 0;
					padding: 0;
					margin: 0 0 20px;
					width: auto;
					border-color: #ccc;
					border-width: 0;
					border-style: solid;
					min-width: 600px;
					height: auto;
				}
				//图片上传样式
				.el-upload-list  {
					//提示语
					.el-upload__tip {
						margin: 7px 0 0;
						color: #666;
						display: flex;
						font-size: 14px;
						justify-content: flex-start;
						align-items: center;
					}
					//外部盒子
					.el-upload--picture-card {
						border: 0px solid #ddd;
						cursor: pointer;
						background-color: #C61C1420;
						border-radius: 30px;
						width: 120px;
						line-height: 70px;
						text-align: center;
						height: 60px;
						//图标
						.el-icon{
							color: #666;
							font-size: 24px;
						}
					}
					.el-upload-list__item {
						border: 0px solid #ddd;
						cursor: pointer;
						background-color: #C61C1420;
						border-radius: 30px;
						width: 120px;
						line-height: 70px;
						text-align: center;
						height: 60px;
					}
				}
			}
		}
	}
	// 按钮盒子
	.formModel_btn_box {
		padding: 0 0 0 150px;
		margin: 20px 0 0;
		display: flex;
		width: 100%;
		align-items: center;
		.formModel_cancel {
			border: 0;
			cursor: pointer;
			border-radius: 30px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			color: #fff;
			background: #C61C1450;
			width: auto;
			font-size: 14px;
			min-width: 100px;
			height: 36px;
		}
		.formModel_cancel:hover {
		}
		
		.formModel_confirm {
			border: 0px solid #f69a28;
			cursor: pointer;
			border-radius: 30px;
			padding: 0 24px;
			margin: 0 20px 0 0;
			color: #fff;
			background: linear-gradient(270deg, rgba(246,154,40,1) 0%, rgba(255,186,101,1) 50%, rgba(246,154,40,1) 100%);
			width: auto;
			font-size: 14px;
			min-width: 100px;
			height: 36px;
		}
		.formModel_confirm:hover {
		}
	}
</style>